.App {
  position: relative;
}
.App.default-theme {
  background-color: rgb(145, 112, 142);
}
/* button-start */
.vbl-theme .app-button {
  border: 1px solid white;
  border-radius: 0px;
  padding: 7px;
  background: transparent;
  color: white;
  text-transform: uppercase;
  transition: background 1s linear;
  letter-spacing: 2px;
  position: relative;
}

.vbl-theme .app-button::after,
.vbl-theme .app-button::before {
  position: absolute;
  content: "";
  width: 15%;
  height: 6px;
  border: 1px solid white;
  background: #0c192c;
  transition: left 0.3s linear, right 0.3s linear;
  border-color: var(--blue-color);
}

.vbl-theme .app-button::after {
  transform: translateY(-50%);
  top: 0px;
  right: 10px;
}

.vbl-theme .app-button::before {
  transform: translateY(50%);
  bottom: 0px;
  left: 10%;
}
.vbl-theme .app-button:hover::before {
  transform: translateY(50%);
  left: 75%;
}
.vbl-theme .app-button:hover::after {
  transform: translateY(-50%);
  right: 75%;
}
.vbl-theme .app-button:hover {
  border-color: var(--red-color);
  cursor: pointer;
  color: var(--red-color);
}
.vbl-theme .app-button.blue:hover {
  border-color: var(--blue-color);
  color: var(--blue-color);
}
.vbl-theme .app-button.blue::after,
.vbl-theme .app-button.blue::before {
  border-color: var(--red-color);
}
.vbl-theme .app-button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.vbl-theme .app-button:disabled::before,
.vbl-theme .app-button:disabled::after {
  border-color: white;
}
.vbl-theme .app-button:disabled:hover {
  box-shadow: none;
  cursor: initial;
}
.vbl-theme .app-input {
  padding: 5px;
  border-radius: 5px;
  border: 2px solid var(--blue-color);
  background-color: var(--main-bg-color);
  color: white;
  font-family: "Comic Sans MS", "Comic Sans", "Arial", cursive;
  outline-color: var(--red-color);
}

.default-theme .app-button {
  background: linear-gradient(273deg, #d5d5d5 0%, #aaaaaa 100%);
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px;
  transition: background 1s linear;
}
.default-theme .app-button:hover {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2) inset;
  cursor: pointer;
}
.default-theme .app-button:disabled {
  opacity: 0.5;
}
.default-theme .app-button:disabled:hover {
  box-shadow: none;
  cursor: initial;
}
.default-theme .app-input {
  padding: 5px;
  border-radius: 5px;
  border: 1px solid black;
}

/* modal-start */
.modal-overlay {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  z-index: 3;
}
.app-modal {
  position: relative;
  background: white;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  width: 400px;
  max-height: 600px;
  overflow-y: auto;
}
.app-modal::-webkit-scrollbar {
  width: 5px;
}
.app-modal::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: #bbb;
}

/* rooms-start */
.rooms-wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.vbl-theme .rooms-header {
  color: var(--red-color);
  font-size: 20px;
  margin-bottom: 5px;
}
.rooms-list {
  height: 150px;
  overflow-y: auto;
  width: 250px;
  border: 1px solid black;
  margin-top: 5px;
}
.vbl-theme .rooms-list {
  border-radius: 10px;
  background: #0c192c;
  color: white;
  border: 2px solid var(--blue-color);
}
.rooms-list li {
  padding: 3px;
}

.rooms-item:hover {
  background: darkslateblue;
  cursor: pointer;
}
.rooms-item.selected {
  background: rgb(63, 51, 146);
}
.rooms-buttons {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 250px;
  margin-top: 10px;
}
