.bubbles {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
  justify-content: space-around;
  background-color: var(--main-bg-color);
}
.bubbles span {
  flex-shrink: 0;
  position: relative;
  margin: 0 4px;
  animation: animate 15s linear infinite;
}
.bubbles.simpleBg span {
  width: 30px;
  height: 30px;
  background: #4fc3dc;
  border-radius: 50%;
  box-shadow: 0 0 0 10px #4fc3dc44, 0 0 50px #4fc3dc, 0 0 100px #4fc3dc;
}
.bubbles.grishakaBg span {
  color: var(--red-color);
}
@keyframes animate {
  0% {
    transform: translateY(100vh) scale(0.1);
  }
  100% {
    transform: translateY(-10vh) scale(1);
  }
}

.bubbles.simpleBg span:nth-child(even) {
  background: var(--red-color);
  box-shadow: 0 0 0 10px #ff2d7544, 0 0 50px var(--red-color),
    0 0 100px var(--red-color);
}

.bubbles.grishakaBg span:nth-child(even) {
  color: var(--blue-color);
}
